ure',{staticClass:"image"},[_c('img',{attrs:{"src":require("./assets/topImage.png")}})])])]),_c('div',{staticClass:"column is-6 justify-content-between"},[_c('section',{staticClass:"hero"},[_c('div',{staticClass:"hero-body is-paddingless"},[_c('h1',{staticClass:"title has-text-white"},[_vm._v("\n                    Discovering and enacting the path\n                    "),_c('br'),_vm._v("to safe artificial general intelligence.\n                  ")]),_c('h2',{staticClass:"subtitle has-text-white is-size-5",staticStyle:{"padding-top":"1em"}},[_vm._v("\n                    We’ve trained a large-scale\n                    unsupervised language model which generates coherent\n                    paragraphs of text, achieves state-of-the-art performance on many language modeling benchmarks,\n                    and performs\n                    rudimentary reading comprehension, machine translation, question answering, and summarization—all\n                    without task-specific training.\n                  ")])])])])])])])])]),_c('section',{staticClass:"section"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"columns"},[_c('div',{staticClass:"column is-half is-offset-one-quarter"},[_c('br'),_c('h1',{staticClass:"title is-4 has-text-grey-dark"},[_vm._v("Introduction")]),_c('hr',{staticClass:"Thin",staticStyle:{"border-top":"1px solid lightgrey"}}),_c('p',[_vm._v("In this project we will use the theory and methods of Network Science to investigate the relationships between fine art painters throughout history. Ideally this analysis will serve several purposes:")]),_c('p',[_vm._v("Firstly it’s meant as an exploration tool. Being interested in art history is tough! How do we find new painters we might like? Going through Wikipedia’s endless lists of painters sorted by name takes it’s toll on even the most enduring of art history enthusiasts. So the point is to make a tool for easier, quicker and more entertaining discovery of artists. Hopefully the graphical layout of the network will assist with this discovery, when compared to other overviews of artists. For example it’s likely that artists located nearby each other are somewhat like each other in style, so if one knows and likes a certain artist or movement, then that could be used as a starting point for exploration. Furthermore the size of the nodes is going to be proportional with the artists’ significance, thereby making it easier not to miss any of the big ones.")]),_c('p',[_vm._v("Secondly art history was purposefully chosen for it’s quality of belonging to the humanities. I find it fascinating to apply quantitative methods to qualitative fields of studies such as art history. Since using these quantitative methods is not the usual way of analysing these fields, it might result in novel discoveries not obvious to the human eye without such tools.")]),_c('p',[_vm._v("Network Science is, in my mind, one of the more useful tools for bridging the gap between the humanities and quantitative analysis. I think it’s usefulness derives from the fact that on a microscopic level - the level of the individual nodes and links - a network is pretty qualitative, the link between two nodes represents a relationship and that’s it. But when looked at from a macroscopic perspective - the level of the entire network - all of these qualitative relationships sum up and results in the emergence of a larger system that lends itself to quantitative analysis. It’s a bit similar to how soap works to bind together fat and water, with one end of the soap molecyle being hydrophilic binding to water and the other end hydrophobic binding to fat, it enables the connection of things that otherwise would not connect.")]),_c('p',[_vm._v("So hopefully the use of network science will allow us discover something we didn’t know about the system of art history through time.")]),_c('br'),_c('br'),_c('h1',{staticClass:"title is-4 has-text-grey-dark"},[_vm._v("Retrieving the Dataset")]),_c('hr',{staticClass:"Thin",staticStyle:{"border-top":"1px solid lightgrey"}}),_c('p',[_vm._v("To analyze the painters we first needed to create a dataset that would serve as the basis for the network. Fortunately Wikipedia has lists of art painters through time sorted by beginning letter of last name. We used these lists to generate a list of all the painters and then used that list to download every painter’s own Wikipedia site. This resulted in a dataset with 3223 painters and a combined length of all of their Wikipedia sites of 36,138,404 characters equivelant to 15,058 standard pages (2400 characters/page).")]),_c('br'),_c('br'),_c('h1',{staticClass:"title is-4 has-text-grey-dark"},[_vm._v("Constructing the Network")]),_c('hr',{staticClass:"Thin",staticStyle:{"border-top":"1px solid lightgrey"}}),_c('p',[_vm._v("Having retrieved the text from each painters Wikipedia site, we then used regular expressions to automatically extract all of the links from each painters site. The links were then filtered, so that only the links pointing to other painters Wikipedia sites were left. We then constructed the graph by first creating a node for each painter in our dataset and then by placing an edge from one painter to another if the painter has a link to the other painter’s Wikipedia site on his site. The idea being that we can use the fact that a painter points to another painter in his wiki-site as a proxy for that painter being influenced by the painter that is pointed to. This way painters with a lot of in-going edges, are painters that are mentioned on many other painters’ wiki-sites and can be thought to have been proportionally infuential.")]),_c('p',[_vm._v("This resulted in a directed graph with 3223 nodes and 10448 edges. Which means that on average each painter’s wiki-site points to 3.24 other painters. The resulting graph is visualized below (click the graph for an interactive version):")]),_c('br'),_c('a',{attrs:{"href":"https://ketelsen.github.io/network/"}},[_c('img',{attrs:{"src":require("./assets/main_graph.png")}})]),_c('br'),_c('br'),_c('br'),_c('h1',{staticClass:"title is-4 has-text-grey-dark"},[_vm._v("Basic Statistics about the Network")]),_c('hr',{staticClass:"Thin",staticStyle:{"border-top":"1px solid lightgrey"}}),_c('h3',{staticClass:"title is-5 has-text-grey-dark"},[_vm._v("Distribution of Birth Year of Painters")]),_c('p',[_vm._v("In order to get to know our dataset a bit better, it could be interesting to look at the distribution of the birth years of our painters. Do we primarely have painters that lived in the distant past or very recently? Let’s take a look:")]),_c('br'),_c('img',{attrs:{"src":require("./assets/graph1.png")}}),_c('br'),_c('br'),_c('p',[_vm._v("Looking at the above histogram we can see that the dataset is primarely comprised of recent painters with a mode at around 1890. Interestingly, we do however see a spike at the beginning of the 17th century. This can probably be attributed to the late renaissance and baroque artist movements, that produced a lot of famous painters.")]),_c('br'),_c('h3',{staticClass:"title is-5 has-text-grey-dark"},[_vm._v("Most Influential Painters")]),_c('p',[_vm._v("Looking at the above histogram we can see that the dataset is primarely comprised of recent painters with a mode at around 1890. Interestingly, we do however see a spike at the beginning of the 17th century. This can probably be attributed to the late renaissance and baroque artist movements, that produced a lot of famous painters.")]),_c('p',[_vm._v("Other algorithms for measuring the centrality of a node (painter) exists as well. The I will show the result of these here as well but not go in to deeper details about how to interpret these other centrality measures. For reference look at the explainer notebook or look up the measures yourself. (eller bare skriv lidt tekst under hver measure).")]),_c('br'),_c('img',{attrs:{"src":require("./assets/graph2.png")}}),_c('br'),_c('br'),_c('p',[_vm._v("The result is as expected with all of the most famous painters having the highest in-degrees (and no unknown painters with unexpectedly high in-going degrees). This is great, it verifies that the network works as it should. It can also be seen how the in-degrees follows an exponential (power) distribution which is a property of a scale free network. (?)")]),_c('p',[_vm._v("The above chart is very simple, but in spite of this, pretty interesting. I personally spent quite some time, just exploring the names of the high-ranking painters. It was interesting to find the painters that were very influential, that I had somehow missed and never heard of.")]),_c('br'),_c('h3',{staticClass:"title is-5 has-text-grey-dark"},[_vm._v("Degree Distributions")]),_c('p',[_vm._v("A central. property of any network is the degree distribution. sting to have a degree distribution To check out the network etc it’s interesting to have a degree distribution To check out the network etc it’s interesting to have a degree distribution To check out the network etc it’s interesting to have a degree distribution To check out the network etc it’s interesting to have a degree distribution.")]),_c('br'),_c('img',{attrs:{"src":require("./assets/graph3.png")}}),_c('br'),_c('br'),_c('br'),_c('h1',{staticClass:"title is-4 has-text-grey-dark"},[_vm._v("Analysis of Art Movements")]),_c('hr',{staticClass:"Thin",staticStyle:{"border-top":"1px solid lightgrey"}}),_c('p',[_vm._v("Looking through the wikipedia pages of the artists’ it became apparent that many, if not most, of them mentioned the art movements that the painters belonged to. In fact, they often mention a few art movements, either because the artist belonged to several or because other related movements were used in the description of the artist. We extracted the movements for each painter and used their frequency (the mode) to determine which movement should be chosen to characterize the specific painter. Sometimes the painters’ wikipedia pages even had a nice “artist infobox” where someone had specifically written the movement(s) of the artist. In those cases that or those movements were used instead of just the mode of mentioned movements. (overvej kort ned på det tekniske her).")]),_c('p',[_vm._v("The below bar chart shows how many painters were defined as belonging to each movement:")]),_c('br'),_c('img',{attrs:{"src":require("./assets/graph4.png")}}),_c('br'),_c('br'),_c('p',[_vm._v("Unsurprisingly, It can be seen that most of the painters are assigned to the biggest movements of the period where most of them lived - around 1850 to 1950.")]),_c('br'),_c('h3',{staticClass:"title is-5 has-text-grey-dark"},[_vm._v("Most Influential Painters from each Movement")]),_c('p',[_vm._v("Now let’s take a look at the most influential (by in-degree) painters from each of the movements:")]),_c('br'),_c('img',{attrs:{"src":require("./assets/table1.png")}}),_c('br'),_c('br'),_c('p',[_vm._v("Make the links clickable. Maybe also add dropdown for number of artists to show. Consider showing the card next to the text since it needs more height and less width. When investigating the individual movement painters it can be seen that When investigating the individual movement painters it can be seen that When investigating the individual movement painters it can be seen that When investigating the individual movement painters it can be seen that When investigating the individual movement painters it can be seen that")]),_c('br'),_c('h3',{staticClass:"title is-5 has-text-grey-dark"},[_vm._v("Timeline of the Movements")]),_c('p',[_vm._v("After extracting and assigning the movements to each painter, we had a dataset with movements and the birth years of the painters assigned to those movements. This made it possible to look at the distributions of the birth years of the painters for each movement. By doing so it was possible to obtain an estimation of the movements active years. 30 years were added to the birth years to to getter a better estimate, as we are not interested in the birth year of the, to the movement, assigned painters but in the years where they were performing. The minimum and maximum values of boxplots, represented by the lines should be ignored as they are obscured by outliers, instead one should look at the range from the 1st to the 3rd quartile represented by the box.")]),_c('br'),_c('img',{attrs:{"src":require("./assets/graph5.png")}}),_c('br'),_c('br'),_c('p',[_vm._v("From the above diagram it can be seen that the estimates of the active years of the movements derived from the painters’ birth years are very accurate. For example: this is this and is really this and so on. (skriv lidt mere om eksempler på hvor godt den har ramt).")]),_c('br'),_c('h3',{staticClass:"title is-5 has-text-grey-dark"},[_vm._v("Network of Movements using Association Rule Mining")]),_c('p',[_vm._v("Creating a network of painters of course allows one to get an idea of how these painters are connected and who influenced who. But since we have the info on the movements connected to the painters we might also have an opportunity to see how the movements are connected to each other and which movement led to other movements etc.")]),_c('p',[_vm._v("In order to investigate the relationships between the movements I used the already collected movement links for each painter. This time however I didn’t just use the most frequent movement but all of them. Because that was excactly what I was interested in - how the different movements were mentioned together on the same painter’s wiki-page. This left me with a set of sets of movements that were mentioned together. Much like the data a supermarket have of items that are bought together when looking at each transaction.")]),_c('p',[_vm._v("To analyse what pairs of items (or pairs of sets of items) are usually bought together and what items imply other items, a supermarket often performs what is known as a market basket analysis AKA association rule mining. Seeing how similar the problems were, I chose to also use association rule mining to find the associations between the different movements and then use those associations as edges in a weighted directed graph of movements. I wanted the directions as I wanted to know which movements had inspired which movements, but they weren’t obvious from the association rule mining itself. I therefore decided that the best proxy would be to use a chronological causality, meaning that when a relationship between two movements existed, the first movement (by average active time) would always be the antecedent and the latter movement would be the consequence. This made sense since a movement couldn’t really lead to another movement that was active before that movement. This resulted in the following network of movements:")]),_c('br'),_c('p',{staticStyle:{"font-size":"0.8em","color":"grey"}},[_vm._v("(click the graph for an interactive version)")]),_c('img',{attrs:{"src":require("./assets/ar_graph.png")}}),_c('br'),_c('br'),_c('p',[_vm._v("(INSERT Description of findings from the graph)")]),_c('br'),_c('img',{attrs:{"src":require("./assets/table2.png")}}),_c('br'),_c('br'),_c('h1',{staticClass:"title is-4 has-text-grey-dark"},[_vm._v("Community Detection")]),_c('hr',{staticClass:"Thin",staticStyle:{"border-top":"1px solid lightgrey"}}),_c('p',[_vm._v("We already have the movements assigned to our painters. The movements can be viewed as communities of painters in their own right. But they’ve been assigned only by looking at the text on the wiki-pages. Therefore, it could be interesting to find communities based on the structure of the network i.e. how the painters are linked to each other. It’s then interesting to see if these communities follow the divisions of the movements or if they find other logics in the network to base their divisions on. It might be that a group of painters from different movements actually have a stronger connection to each other than to other painters from their own movement. The network has been visualized with the communities added as a colors below. (maybe several of them).")]),_c('br'),_c('img',{attrs:{"src":require("./assets/main_graph.png")}}),_c('br'),_c('br'),_c('p',[_vm._v("Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities Comment on the found communities .")]),_c('br'),_c('img',{attrs:{"src":require("./assets/graph6.png")}}),_c('br'),_c('br'),_c('br'),_c('img',{attrs:{"src":require("./assets/table3.png")}}),_c('br'),_c('br'),_c('br'),_c('h1',{staticClass:"title is-4 has-text-grey-dark"},[_vm._v("TF-IDF")]),_c('hr',{staticClass:"Thin",staticStyle:{"border-top":"1px solid lightgrey"}}),_c('p',[_vm._v("Having retrieved the text from each painters Wikipedia site, we then used regular expressions to automatically extract all of the links from each painters site. The links were then filtered, so that only the links pointing to other painters Wikipedia sites were left. We then constructed the graph by first creating a node for each painter in our dataset and then by placing an edge from one painter to another if the painter has a link to the other painter’s Wikipedia site on his site. The idea being that we can use the fact that a painter points to another painter in his wiki-site as a proxy for that painter being influenced by the painter that is pointed to. This way painters with a lot of in-going edges, are painters that are mentioned on many other painters’ wiki-sites and can be thought to have been proportionally infuential.")]),_c('p',[_vm._v("This resulted in a directed graph with 3223 